<template>
    <div class="theme-colours">
      <h4>Change Theme Colours</h4>
      <p>Click on the buttons to change the selected colour.</p>
      <v-row>
        <v-col v-for="color in themeColours" :key="color">
            <v-btn class="theme-color" :color="color" :disabled="show" @click="openPicker(color)" elevation="20">
                <v-badge :content="color" bottom></v-badge>
            </v-btn>
        </v-col>
      </v-row>
      <div v-if="show">
        <h5>Change {{colorToUpdate}} colour</h5>
        <v-row>
            <v-color-picker v-model="color"></v-color-picker>
            <v-btn @click="show = false;">Close</v-btn>
        </v-row>
      </div>
  </div>
</template>

<script>
export default {
    computed: {
        color: {
            get () {
                return this.$vuetify.theme.themes[this.theme][this.colorToUpdate]
            },
            set (val) {
                this.$vuetify.theme.themes[this.theme][this.colorToUpdate] = val;
            },
        }
    },
    data: function() {
        return {
            colorToUpdate: 'primary',
            show: false,
            themeColours: ['primary', 'secondary', 'accent', 'error', 'info', 'success', 'warning', 'text', 'card']
        }
    },
    methods: {
      openPicker: function(color) {
        this.colorToUpdate = color;
        this.show = true;
      }
    },
    name: 'ThemeColorPicker',
    props: {
        theme: String
    }
}
</script>

