<template>
  <div class="theme-switch">
    <h4>Switch Theme</h4>
    <v-btn-toggle v-model="currentTheme" rounded>
        <v-btn value="light" @click="setLightTheme">Light</v-btn>
        <v-btn value="dark" @click="setDarkTheme">Dark</v-btn>
    </v-btn-toggle>
  </div>
</template>

<script>
export default {
    data: function() {
        return {
            currentTheme: this.value
        }
    },
    methods: {
      setDarkTheme: function() {
        this.$vuetify.theme.dark = true;
        this.$emit('input', 'dark')
      },
      setLightTheme: function() {
        this.$vuetify.theme.dark = false;
        this.$emit('input', 'light')
      }
    },
    name: 'ThemePicker',
    props: {
        value: String
    }
}
</script>

